import React, { Component } from 'react';
import { Table, Divider, message, Modal, Select, Button, Input, Card } from 'antd';

import { PAGE_SIZE } from '../../utils/constants';
import LinkButton from '../../components/link-button';
import { reqNotices, reqDeleteNotice, reqSearchNotices } from '../../api'
const { Column } = Table;
const Option = Select.Option



class ManageNotice extends Component {
    state = {
        total: 0, // 
        notices: [], // 
        loading: false,//
        searchName: '', // 
        searchType: 'noticeTitle', // 
    }
    //
    getNotices = async (pageNum) => {
        //loadingtrue
        this.setState({ loading: true })
        const { searchName, searchType } = this.state
        let result
        if (searchName) {
            result = await reqSearchNotices({ pageNum, pageSize: PAGE_SIZE, searchName, searchType })
        } else { // 
            result = await reqNotices(pageNum, PAGE_SIZE)
        }
        // const result = await reqCheckedArticles(pageNum, PAGE_SIZE,searchName,searchType)
        //loadingfalse
        this.setState({ loading: false })
        if (result.status === 0) {
            const { total, list } = result.data
            console.log(list)
            this.setState({
                total,
                notices: list
            })
        }
    }
    /*

*/
    deleteArticle = (notice) => {
        Modal.confirm({
            title: `****[${notice.title}]****?`,
            onOk: async () => {
                const result = await reqDeleteNotice(notice.title)
                if (result.err === 0) {
                    message.success('!')
                    this.getNotices(1)
                }else if (result.err === -999) {
                    message.error('')
                } else if (result.err === -888) {
                    message.error(',')
                } else {
                    message.error('')
                }
            }
        })
    }

    componentDidMount() {
        this.getNotices(1)
    }
    render() {
        const { notices, total, loading, searchName, searchType } = this.state

        const head = (
            <span>
                <Select
                    value={searchType}
                    style={{ width: 150 }}
                    onChange={value => this.setState({ searchType: value })}
                >
                    <Option value='noticeTitle'></Option>
                    {/* <Option value='articleCategory'></Option> */}
                    <Option value='noticeAuthor'></Option>
                    <Option value='noticeContent'></Option>
                    <Option value='noticeDepartment'></Option>

                </Select>
                <Input
                    placeholder=''
                    style={{ width: 150, margin: '0 15px' }}
                    value={searchName}
                    onChange={event => this.setState({ searchName: event.target.value })}
                />
                <Button type='primary' onClick={() => this.getNotices(1)}></Button>
            </span>
        )
        return (
            <Card title={head}>
                <Table

                    rowKey='_id'
                    loading={loading}
                    //
                    pagination={{
                        total,
                        defaultPageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        onChange: this.getNotices,

                    }}
                    dataSource={notices}>
                    <Column title="" dataIndex="title" />
                    <Column title="" dataIndex="author" />
                    <Column title="" dataIndex="department" />
                    <Column title="" dataIndex='time' />
                    <Column
                        title=""
                        key="action"
                        render={(notice) => (
                            <span>
                                <LinkButton onClick={() => this.props.history.push('/notice/detail', { notice })}></LinkButton>
                                <Divider type="vertical" />
                                <LinkButton onClick={() => this.props.history.push('/noticesManage/add' , notice)}></LinkButton>
                                <Divider type='vertical'/>
                                <LinkButton onClick={() => this.deleteArticle(notice)}></LinkButton>
                            </span>
                        )}
                    />
                </Table>
            </Card>
        )

    }
}
export default ManageNotice;