import React, { Component } from 'react';
import { Card, Form, Input, Button, message, Select } from 'antd';
// import RichTextEditor from '../newsmanage/RichTextEditor'
import { BASE_ZZ_DEPARTMENT } from '../../utils/constants'
import { reqAddOrUpdateNotice } from '../../api/index';
import EditorDemo from '../newsmanage/RichText';
const { Item } = Form
const { Option } = Select;


class WriteNotice extends Component {
    constructor(props) {
        super(props)
        // ref
        this.editor = React.createRef()
    }
    submit = () => {
        // , , 
        this.props.form.validateFields(async (error, values) => {
            if (!error) {
                //1.23.
                const { title, author, department, category } = values
                const content = this.editor.current.getDetail()
                const notice = { title, author, department, category, content }
                console.log(notice)
                // 2., _id
                if (this.isUpdate) { notice._id = this.notice._id }
                // 3. /
                const result = await reqAddOrUpdateNotice(notice)
                // 3. 
                if (result.err === 0) {
                    message.success(`${this.isUpdate ? '' : ''}!`)
                    this.props.history.goBack()
                } else if (result.err === -999) {
                    message.error('')
                } else if (result.err === -888) {
                    message.error(',')
                }
                else {
                    message.error(`${this.isUpdate ? '' : ''}!`)
                }
            }
        })

    }
    componentWillMount() {
        // state
        const notice = this.props.location.state  // , 
        // 
        console.log(notice)
        this.isUpdate = !!notice
        // (, {})
        this.notice = notice || {}
    }
    render() {
        const formItemLayout = {
            labelCol: { xs: { span: 2 }, sm: { span: 2 }, },
            wrapperCol: { xs: { span: 20 }, sm: { span: 20 }, },
        };

        const { getFieldDecorator } = this.props.form;
        return (
            <Card>
                <Form {...formItemLayout}>
                    <Item label="">
                        {getFieldDecorator('category', {
                            initialValue: this.notice.category,
                            rules: [{ required: true, message: '!' }],
                        })(
                            <Select
                                placeholder=""
                                onChange={this.handleSelectChange}
                            >
                                <Option value=""></Option>
                            </Select>,
                        )}
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('title', {
                                initialValue: this.notice.title,
                                rules: [
                                    { required: true, message: 'title' },
                                    { max: 70, message: '' }//
                                ]
                            })(<Input placeholder='title' />)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('author', {
                                initialValue: this.notice.author,
                                rules: [
                                    { required: true, message: '' },
                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('department', {
                                initialValue: this.notice.department,
                                rules: [
                                    { required: true, message: '' },
                                    { type: "enum", enum: BASE_ZZ_DEPARTMENT }//

                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>

                    <Item label="" labelCol={{ span: 2 }} wrapperCol={{ span: 20 }}>
                        {/* <RichTextEditor ref={this.editor} detail = {this.notice.content}/> */}
                        <EditorDemo ref={this.editor} detail={this.notice.content} />
                    </Item>

                    <Item>
                        <Button style={{ marginLeft: '10%' }}
                            type='primary'
                            block onClick={this.submit}></Button>
                    </Item>
                </Form>
            </Card>
        )
    }
}
export default Form.create()(WriteNotice)