import React, { Component } from 'react'
import { Redirect } from 'react-router-dom'
import {
  Form,
  Icon,
  Input,
  Button,
  message
} from 'antd'
import './login.less'
// import logo from '../../assets/images/logo.png'
import { reqLogin } from '../../api'
import memoryUtils from '../../utils/memoryUtils'
import storageUtils from '../../utils/storageUtils'


const Item = Form.Item // import


/*

 */
class Login extends Component {

  handleSubmit = (event) => {

    // 
    event.preventDefault()

    // 
    this.props.form.validateFields(async (err, values) => {
      // 
      if (!err) {
        // console.log('ajax', values)
        // 
        const { username, password } = values
        const result = await reqLogin(username, password) // {status: 0, data: user}  {status: 1, msg: 'xxx'}
        console.log('', result)
        if (result.status === 0) { // 
          // 
          message.success('')
          // user
          const user = result.data
          memoryUtils.user = user // 
          storageUtils.saveUser(user) // local

          //  ()
          this.props.history.replace('/')

        } else { // 
          // 
          message.error(result.msg)
        }

      } else {
        console.log('!')
      }
    });

    // form
    // const form = this.props.form
    // // 
    // const values = form.getFieldsValue()
    // console.log('handleSubmit()', values)
  }

  /*
  
  */
  /*
   /
     1). 
     2). 4
     3). 12
     4). 
    */
  validatePwd = (rule, value, callback) => {
    console.log('validatePwd()', rule, value)
    if (!value) {
      callback('')
    } else if (value.length < 4) {
      callback('4')
    } else if (value.length > 12) {
      callback('12')
    } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
      callback('')
    } else {
      callback() // 
    }
    // callback('xxxx') // , 
  }

  render() {

    // , 
    const user = memoryUtils.user
    if (user && user._id) {
      return <Redirect to='/' />
    }

    // form
    const form = this.props.form
    const { getFieldDecorator } = form;

    return (
      <div className="login">
        <header className="login-header">
          {/* <img src={logo} alt="logo"/> */}
          <h1></h1>
        </header>
        <section className="login-content">
          <h2></h2>
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Item>
              {
                /*
              /
                1). 
                2). 4
                3). 12
                4). 
               */
              }
              {
                getFieldDecorator('username', { // : 
                  // : 
                  rules: [
                    { required: true, whitespace: true, message: '' },
                    { min: 1, message: '4' },
                    { max: 12, message: '12' },
                    {/* { pattern: /^[a-zA-Z0-9_]+$/, message: '' }, */ }
                  ],
                  initialValue: 'admin', // 
                })(
                  <Input
                    prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    placeholder=""
                  />
                )
              }
            </Item>
            <Form.Item>
              {
                getFieldDecorator('password', {
                  rules: [
                    {
                      validator: this.validatePwd
                    }
                  ]
                })(
                  <Input
                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    type="password"
                    placeholder=""
                  />
                )
              }

            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" className="login-form-button">
                
              </Button>
            </Form.Item>
          </Form>
        </section>
      </div>
    )
  }
}

/*
1. 
    1). 
        a. 
        b. 
    2). 
        a. : setTimeout()/setInterval()
        b. Promise: Promise(() => {}) then(value => {}, reason => {})
        c. : forEach()/filter()/map()/reduce()/find()/findIndex()
        d. bind()
        e. Form.create()() / getFieldDecorator()()
    3). , 

2. 
    1). 
    2). (), (), 
    3). : 
    4). : , 
 */
/*
Form: Form(Login)
Form: form
 */
const WrapLogin = Form.create()(Login)
export default WrapLogin
