import React, { Component } from 'react';
import { Table, Divider, message, Modal, Select, Button, Input, Card } from 'antd';
import { PAGE_SIZE } from '../../utils/constants';
import LinkButton from '../../components/link-button';
import { reqCheckedArticles, reqDeleteArticle, reqCheckedSearchArticles } from '../../api'
const { Column } = Table;
const Option = Select.Option



class ManageNews extends Component {
    state = {
        total: 0, // 
        articles: [], // 
        loading: false,//
        searchName: '', // 
        searchType: 'articleTitle', // 
    }
    //
    getNews = async (pageNum) => {
        //loadingtrue
        this.setState({ loading: true })
        const { searchName, searchType } = this.state
        let result
        if (searchName) {
            result = await reqCheckedSearchArticles({ pageNum, pageSize: PAGE_SIZE, searchName, searchType })
        } else { // 
            result = await reqCheckedArticles(pageNum, PAGE_SIZE)
        }
        // const result = await reqCheckedArticles(pageNum, PAGE_SIZE,searchName,searchType)
        //loadingfalse
        this.setState({ loading: false })
        if (result.status === 0) {
            const { total, list } = result.data
            console.log(list)
            this.setState({
                total,
                articles: list
            })
        }
    }
    /*

*/
    deleteArticle = (article) => {
        Modal.confirm({
            title: `****[${article.title}]****?`,
            onOk: async () => {
                const result = await reqDeleteArticle(article.title)
                if (result.err === 0) {
                    message.success('!')
                    this.getNews(1)
                } else if (result.err === -999) {
                    message.error('')
                } else if (result.err === -888) {
                    message.error(',')
                } else {
                    message.error('other error')
                }
            }
        })
    }


    componentDidMount() {
        this.getNews(1)
    }
    render() {
        const { articles, total, loading, searchName, searchType } = this.state

        const head = (
            <span>
                <Select
                    value={searchType}
                    style={{ width: 150 }}
                    onChange={value => this.setState({ searchType: value })}
                >
                    <Option value='articleTitle'></Option>
                    {/* <Option value='articleCategory'></Option> */}
                    <Option value='articleAuthor'></Option>
                    <Option value='articleContent'></Option>
                    <Option value='articleDepartment'></Option>

                </Select>
                <Input
                    placeholder=''
                    style={{ width: 150, margin: '0 15px' }}
                    value={searchName}
                    onChange={event => this.setState({ searchName: event.target.value })}
                />
                <Button type='primary' onClick={() => this.getNews(1)}></Button>
            </span>
        )
        return (
            <Card title={head}>
                <Table

                    rowKey='_id'
                    loading={loading}
                    //
                    pagination={{
                        total,
                        defaultPageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        onChange: this.getNews,

                    }}
                    dataSource={articles}>
                    <Column title="" dataIndex="title" />
                    <Column title="" dataIndex="author" />
                    <Column title="" dataIndex="department" />
                    <Column title="" dataIndex='time' />
                    <Column
                        title=""
                        key="action"
                        render={(article) => (
                            <span>
                                <LinkButton onClick={() => this.props.history.push('/article/detail', { article })}></LinkButton>
                                <Divider type="vertical" />
                                <LinkButton onClick={() => this.props.history.push('/writenews', article)}></LinkButton>
                                <Divider type='vertical' />
                                <LinkButton onClick={() => this.deleteArticle(article)}></LinkButton>

                            </span>
                        )}
                    />
                </Table>
            </Card>
        )

    }
}
export default ManageNews;