import React, { Component } from 'react';
import { Card, Form, Input, Button, message } from 'antd';
import { BASE_ZZ_DEPARTMENT } from '../../utils/constants' //
import { reqAddOrUpdateDepartmentMessage } from '../../api/index';
import EditorDemo from '../newsmanage/RichText';
const { Item } = Form
//

class WriteMessage extends Component {
    constructor(props) {
        super(props)
        // ref
        this.editor = React.createRef()
    }
    submit = () => {
        // , , 
        this.props.form.validateFields(async (error, values) => {
            if (!error) {
                //1.23.
                // console.log('ok')
                const { title, author, department } = values
                const content = this.editor.current.getDetail()
                const Dmessage = { title, author, department, content }
                // 2., _id
                if (this.isUpdate) {
                    Dmessage._id = this.Dmessage._id
                }

                // 2. /
                const result = await reqAddOrUpdateDepartmentMessage(Dmessage)

                // 3. 
                if (result.err === 0) {
                    message.success(`${this.isUpdate ? '' : ''}!`)
                    this.props.history.push('/success')
                } else if (result.err === -999) {
                    message.error('')
                } else if (result.err === -888) {
                    message.error(',')
                }
                else {
                    message.error(`${this.isUpdate ? '' : ''}!`)
                }
            }
        })

    }
    componentWillMount() {
        // state
        const Dmessage = this.props.location.state  // , 
        // 
        console.log(Dmessage)
        this.isUpdate = !!Dmessage
        // (, {})
        this.Dmessage = Dmessage || {}
    }
    render() {
        const formItemLayout = {
            labelCol: {
                xs: { span: 2 },//
                sm: { span: 2 },
            },
            wrapperCol: {
                xs: { span: 20 },//
                sm: { span: 20 },
            },
        };

        const { getFieldDecorator } = this.props.form;
        return (
            <Card>
                <Form {...formItemLayout}>
                    <Item label='title'>
                        {
                            getFieldDecorator('title', {
                                initialValue: this.Dmessage.title,
                                rules: [
                                    { required: true, message: 'title' },
                                    { max: 50, message: '' }//
                                ]
                            })(<Input placeholder='title' />)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('author', {
                                initialValue: this.Dmessage.author,
                                rules: [
                                    { required: true, message: '' },
                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('department', {
                                initialValue: this.Dmessage.department,
                                rules: [
                                    { required: true, message: '' },
                                    { type: "enum", enum: BASE_ZZ_DEPARTMENT }//

                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>

                    <Item label="" labelCol={{ span: 2 }} wrapperCol={{ span: 20 }}>
                        {/* <RichTextEditor ref={this.editor} detail = {this.Dmessage.content}/> */}
                        <EditorDemo ref={this.editor} detail={this.Dmessage.content} />
                    </Item>

                    <Item>
                        <Button style={{ marginLeft: '10%' }}
                            type='primary'
                            block onClick={this.submit}></Button>
                    </Item>
                </Form>
            </Card>
        )
    }
}
export default Form.create()(WriteMessage)