import { Button, Card, Divider, message, Modal, Table } from 'antd';
import React, { Component } from 'react'
import { reqMailUncheckedMessages, reqMailUpdateStatus } from '../../api'
import LinkButton from '../../components/link-button';
import { PAGE_SIZE } from '../../utils/constants';
const { Column } = Table;


export default class MailCheck extends Component {
    state = {
        total: 0, // 
        UnCheckedMails: [], // 
        loading: false,//
    }
    //
    getUnCheckedMails = async (pageNum) => {
        //loadingtrue
        this.setState({ loading: true })
        let result
        result = await reqMailUncheckedMessages(pageNum, PAGE_SIZE)
        this.setState({ loading: false })
        if (result.status === 0) {
            const { total, list } = result.data
            this.setState({ total, UnCheckedMails: list })
            console.log(list)
        }
    }
    //mail
    checkedMail = (mail) => {
        Modal.confirm({
            content: `${mail.department}${mail.name}:${mail.title}?`,
            onOk: async () => {
                const result = await reqMailUpdateStatus(mail.content)
                console.log(result)
                if (result.err === 0) {
                    message.success('!')
                    this.getUnCheckedMails(1)
                } else if (result.err === -888) {
                    message.error('')
                } else if (result.err === -999) {
                    message.error('')
                } else {
                    message.error('')
                }
            }
        })
    }

    componentDidMount() {
        this.getUnCheckedMails(1)
    }
    render() {
        const { total, UnCheckedMails, loading } = this.state

        return (
            <Card>
                <Table rowKey='_id' loading={loading}
                    pagination={{
                        total, defaultPageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        onChange: this.getUnCheckedMails,
                    }}
                    dataSource={UnCheckedMails} >
                    <Column title="" dataIndex="leader"
                        render={(leader) => (leader) === "zz" ? '' : (leader) === "zw" ? '' : (leader) === 'zr' ? '' : ''} />
                    <Column title="" dataIndex="title" />
                    <Column title="" dataIndex="name" />
                    <Column title="" dataIndex="department" />
                    <Column title="" dataIndex='time' />
                    <Column
                        title=""
                        key="action"
                        render={(Mailmessage) => (
                            <span>
                                <LinkButton onClick={() => this.props.history.push('/mail/detail', { Mailmessage })}></LinkButton>
                                <Divider type="vertical" />
                                <Button type='primary' onClick={() => this.checkedMail(Mailmessage)}></Button>
                            </span>
                        )}
                    />
                </Table>
            </Card>
        )
    }
}
