import React, { Component } from 'react';
import { Table, Divider, message, Modal, Select, Button, Input, Card } from 'antd';
import { PAGE_SIZE } from '../../utils/constants';
import LinkButton from '../../components/link-button';
import { reqMailMessages, reqDeleteMail, reqSearchMails } from '../../api'
const { Column } = Table;
const Option = Select.Option



class MailManage extends Component {
    state = {
        total: 0, // 
        Mailmessages: [], // 
        loading: false,//
        searchName: '', // 
        searchType: 'mailTitle', // 
    }
    //
    getMail = async (pageNum) => {
        //loadingtrue
        this.setState({ loading: true })
        console.log(this.state)
        const { searchName, searchType } = this.state
        let result
        if (searchName) {
            result = await reqSearchMails({ pageNum, pageSize: PAGE_SIZE, searchName, searchType })
        } else { // 
            result = await reqMailMessages(pageNum, PAGE_SIZE)
        }
        this.setState({ loading: false })
        if (result.status === 0) {
            const { total, list } = result.data
            console.log(list)
            this.setState({
                total,
                Mailmessages: list
            })
        }
    }
    /*

*/
    deleteMail = (Mailmessage) => {
        Modal.confirm({
            title: `[${Mailmessage.title}]?`,
            onOk: async () => {
                const result = await reqDeleteMail(Mailmessage._id)
                if (result.err === 0) {
                    message.success('!')
                    this.getMail(1)
                } else if (result.err === -888) {
                    message.error(',')
                }
                else if (result.err === -999) {
                    message.error('')
                }
                else {
                    message.error('other error')
                }
            }
        })
    }
    componentDidMount() {
        this.getMail(1)
    }
    render() {
        const { Mailmessages, total, loading, searchName, searchType } = this.state

        const head = (
            <span>
                <Select
                    value={searchType}
                    style={{ width: 150 }}
                    onChange={value => this.setState({ searchType: value })}
                >
                    <Option value='mailTitle'></Option>
                    <Option value='mailAuthor'></Option>
                    <Option value='mailContent'></Option>
                    <Option value='mailDepartment'></Option>

                </Select>
                <Input
                    placeholder=''
                    style={{ width: 150, margin: '0 15px' }}
                    value={searchName}
                    onChange={event => this.setState({ searchName: event.target.value })}
                />
                <Button type='primary' onClick={() => this.getMail(1)}></Button>
            </span>
        )
        return (
            <Card title={head}>
                <Table
                    rowKey='_id'
                    loading={loading}
                    //
                    pagination={{
                        total,
                        defaultPageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        onChange: this.getMail,

                    }}
                    dataSource={Mailmessages}>
                    <Column title='' dataIndex='leader'
                        render={(leader) => (leader) === "zz" ? '' : (leader) === "zw" ? '' : (leader) === 'zr' ? '' : ''} />
                    <Column title="" dataIndex="title" />
                    <Column title="" dataIndex="name" />
                    <Column title="" dataIndex="department" />
                    <Column title="" dataIndex='time' />
                    <Column
                        title=""
                        key="action"
                        render={(Mailmessage) => (
                            <span>
                                <LinkButton onClick={() => this.props.history.push('/mail/detail', { Mailmessage })}></LinkButton>
                                <Divider type='vertical' />
                                <LinkButton onClick={() => this.deleteMail(Mailmessage)}></LinkButton>
                            </span>
                        )}
                    />
                </Table>
            </Card>
        )

    }
}
export default MailManage;