import React, { Component } from 'react';
import { Card, Form, Input, Button, message, Select } from 'antd';
// import RichTextEditor from '../newsmanage/RichTextEditor'
import { BASE_ALL_DEPARTMENT } from '../../utils/constants'
import { reqAddOrUpdateBookFile } from '../../api/index';
import EditorDemo from '../newsmanage/RichText';
const { Item } = Form
const { Option } = Select;

class WriteBookFile extends Component {
    constructor(props) {
        super(props)
        // ref
        this.editor = React.createRef()
    }
    submit = () => {
        // , , 
        this.props.form.validateFields(async (error, values) => {
            if (!error) {
                //1.23.
                // console.log('ok')
                const { title, author, department, category } = values
                const content = this.editor.current.getDetail()
                const bookfile = { title, author, department, category, content }
                // , _id
                if (this.isUpdate) {
                    bookfile._id = this.bookfile._id
                }

                // 2. /
                const result = await reqAddOrUpdateBookFile(bookfile)

                // 3. 
                if (result.err === 0) {
                    message.success(`${this.isUpdate ? '' : ''}!`)
                    this.props.history.goBack()
                } else {
                    message.error(`${this.isUpdate ? '' : ''}!`)
                }
                // console.log(bookfile)
                // const result = await reqAddBookFiles(bookfile)
                // console.log(result)
                // if (result.err === 0) {
                //     message.success('')
                //     this.props.history.push('/bookfile/detail', { bookfile })
                // }
                // else {
                //     message.error('')
                // }
            }
        })

    }
    componentWillMount() {
        // state
        const bookfile = this.props.location.state  // , 
        // 
        this.isUpdate = !!bookfile
        // (, {})
        this.bookfile = bookfile || {}
    }
    render() {
        const formItemLayout = {
            labelCol: {
                xs: { span: 2 },//
                sm: { span: 2 },
            },
            wrapperCol: {
                xs: { span: 20 },//
                sm: { span: 20 },
            },
        };

        const { getFieldDecorator } = this.props.form;
        return (
            <Card>
                <Form {...formItemLayout}>
                    <Item label="">
                        {getFieldDecorator('category', {
                            initialValue:this.bookfile.category,
                            rules: [{ required: true, message: '!' }],
                        })(
                            <Select
                                placeholder=""
                                onChange={this.handleSelectChange}
                            >
                                <Option value=""></Option>
                            </Select>,
                        )}
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('title', {
                                initialValue: this.bookfile.title,
                                rules: [
                                    { required: true, message: 'title' },
                                    { max: 27, message: '' }//
                                ]
                            })(<Input placeholder='title' />)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('author', {
                                initialValue: this.bookfile.author,
                                rules: [
                                    { required: true, message: '' },
                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('department', {
                                initialValue: this.bookfile.department,
                                rules: [
                                    { required: true, message: '' },
                                    { type: "enum", enum: BASE_ALL_DEPARTMENT }//

                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>

                    <Item label="" labelCol={{ span: 2 }} wrapperCol={{ span: 20 }}>
                        {/* <RichTextEditor /> */}
                        <EditorDemo ref={this.editor} detail={this.bookfile.content}/>
                    </Item>

                    <Item>
                        <Button style={{ marginLeft: '10%' }}
                            type='primary'
                            block onClick={this.submit}></Button>
                    </Item>
                </Form>
            </Card>
        )
    }
}
export default Form.create()(WriteBookFile)