import React, { Component } from 'react';
import './index.css';
import { Card, Form, Input, Button, message } from 'antd';
// import {  Carousel } from 'antd';
import PicWall from './PicWall';
import {reqAddBannerpic} from '../../api'
const { Item } = Form

class BannerManage extends Component {
    constructor(props) {
        super(props)
        // ref
        this.pw = React.createRef()
    }

    submit = () => {
        // , , 
        this.props.form.validateFields(async (error, values) => {
            if (!error) {
                //1.23.
                // console.log('ok')

                const { url_address } = values

                const url = this.pw.current.getImgs()
                const bannerpic = {  url, url_address }
                // console.log(bannerpic)
                const result = await reqAddBannerpic(bannerpic)
                console.log(result)
                if (result.err === 0) {
                    message.success('')
                    // 

                    this.props.history.push('/success', { bannerpic })
                }
                else {
                    message.error('')
                }
            }
        })

    }
    // state = {
    //     urls:[],
    // }
    // getUrl = async ()=>{
    //     const result = await reqGetBannerImg()
    //     if (result.status === 0) {
    //         const urls = result.list
    //         this.setState({
    //             urls
    //         })
    //     }
    // }
    // componentDidMount(){
    //     this.getUrl()
    // }
//5
    render() {
        const formItemLayout = {
            labelCol: {
                xs: { span: 2 },//
                sm: { span: 2 },
            },
            wrapperCol: {
                xs: { span: 20 },//
                sm: { span: 20 },
            },
        };
        const { getFieldDecorator } = this.props.form;
        return (
            <Card>
                <Form {...formItemLayout}>
                    <Item label=''>
                        {
                            getFieldDecorator('url_address', {
                                initialValue: '',
                                rules: [
                                    { required: true, message: '' },
                                    { max: 300, message: '' }//
                                ]
                            })(<Input placeholder='' />)
                        }
                    </Item>
                  
                    <Item label=''>
                        <PicWall ref={this.pw} />
                    </Item>
                    <Item>
                        <Button style={{ marginLeft: '10%' }}
                            type='primary'
                            block onClick={this.submit}></Button>
                    </Item>
                </Form>
            </Card>
        )
    }
}
export default Form.create()(BannerManage)