import React, { Component } from 'react';
import PictureWall from './pictures-wall'
import { Card, Form, Input, Button, message, Select } from 'antd';
// import RichTextEditor from './RichTextEditor';
import EditorDemo from './RichText.js'
import { BASE_ALL_DEPARTMENT } from '../../utils/constants'
import { reqAddOrUpdateArticle } from '../../api/index';
import FileWall from './FileWall';
const { Item } = Form
// const { TextArea } = Input
const { Option } = Select;


class WriteNews extends Component {
    constructor(props) {
        super(props)
        // ref
        this.pw = React.createRef() //
        this.editor = React.createRef() //
        this.fw = React.createRef() //
    }

    submit = () => {
        // , , 
        this.props.form.validateFields(async (error, values) => {
            if (!error) {
                //1.23.
                // console.log('ok')

                const { title, author, department, category } = values//
                const thumbnail = this.pw.current.getImgs()  //
                const content = this.editor.current.getDetail()  //
                const download_url = this.fw.current.getUrls()//
                const article = { title, author, department, thumbnail, category, content, download_url }
                // , _id
                if (this.isUpdate) {
                    article._id = this.article._id
                }

                // 2. /
                const result = await reqAddOrUpdateArticle(article)
                console.log(result.err)
                // 3. 
                if (result.err === 0) {
                    message.success(`${this.isUpdate ? '' : ''}!`)
                    this.props.history.push('/success', { article })
                } else if (result.err === -999) {
                    message.error('')
                } else if (result.err === -888) {
                    message.error(',')
                }
                else {
                    message.error(`${this.isUpdate ? '' : ''}!`)
                }

            }
        })

    }
    componentWillMount() {
        // state
        const article = this.props.location.state  // , 
        // 
        this.isUpdate = !!article
        // (, {})
        this.article = article || {}
    }

    render() {
        //    console.log(this.article.thumbnail[0])
        const formItemLayout = {
            labelCol: {
                xs: { span: 2 },//
                sm: { span: 2 },
            },
            wrapperCol: {
                xs: { span: 20 },//
                sm: { span: 20 },
            },
        };

        const { getFieldDecorator } = this.props.form;
        return (
            <Card>
                <Form {...formItemLayout}>
                    <Item label="">
                        {getFieldDecorator('category', {
                            initialValue: this.article.category,
                            rules: [{ required: true, message: '!' }],
                        })(
                            <Select
                                placeholder=""
                                onChange={this.handleSelectChange}
                            >
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>,
                        )}
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('title', {
                                initialValue: this.article.title,
                                rules: [
                                    { required: true, message: '' },
                                    { max: 50, message: '' }//
                                ]
                            })(<Input placeholder='' />)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('author', {
                                initialValue: this.article.author,
                                rules: [
                                    { required: true, message: '' },
                                    { whitespace: true }
                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('department', {
                                initialValue: this.article.department,
                                rules: [
                                    { required: true, message: '' },
                                    { type: "enum", enum: BASE_ALL_DEPARTMENT }//

                                ]
                            })(<Input placeholder='' />)
                        }

                    </Item>
                    <Item label=''>
                        <PictureWall ref={this.pw} imgs={this.article.thumbnail} />
                    </Item>
                    <Item label="" labelCol={{ span: 2 }} wrapperCol={{ span: 20 }}>
                        <EditorDemo ref={this.editor} detail={this.article.content} />
                    </Item>
                    <Item label=''>
                        <FileWall ref={this.fw} />
                    </Item>
                    <Item>
                        <Button style={{ marginLeft: '10%' }}
                            type='primary'
                            block onClick={this.submit}></Button>
                    </Item>
                </Form>
            </Card>
        )
    }
}
export default Form.create()(WriteNews)


