import React, { Component } from 'react';
import { Table, Divider, message, Modal, Select, Button, Input, Card, } from 'antd';
import { PAGE_SIZE } from '../../utils/constants';
import LinkButton from '../../components/link-button';
import { reqVideos, reqDeleteVideos, reqSearchVideos } from '../../api'
const { Column } = Table;
const Option = Select.Option



class VideoManage extends Component {
    state = {
        total: 0, // 
        videos: [], // 
        loading: false,//
        searchName: '', // 
        searchType: 'videoName', // 
    }
    //
    getVideos = async (pageNum) => {
        //loadingtrue
        this.setState({ loading: true })
        const { searchName, searchType } = this.state
        let result
        if (searchName) {
            result = await reqSearchVideos({ pageNum, pageSize: PAGE_SIZE, searchName, searchType })
        } else { // 
            result = await reqVideos(pageNum, PAGE_SIZE)
        }
        this.setState({ loading: false })
        if (result.status === 0) {
            const { total, list } = result.data
            console.log(list)
            this.setState({
                total,
                videos: list
            })
        }
    }
    /*

*/
    deleteVideo = (video) => {
        Modal.confirm({
            title: `****[${video.title}]****?`,
            onOk: async () => {
                const result = await reqDeleteVideos(video.title)
                if (result.err === 0) {
                    message.success('!')
                    this.getVideos(1)
                } else if (result.err === -888) {
                    message.error(',')
                } else {
                    message.error('other error')
                }
            }
        })
    }

    componentDidMount() {
        this.getVideos(1)
    }
    render() {
        const { videos, total, loading, searchName, searchType } = this.state

        const head = (
            <span>
                <Select
                    value={searchType}
                    style={{ width: 150 }}
                    onChange={value => this.setState({ searchType: value })}
                >
                    <Option value='videoName'></Option>
                    <Option value='videoDesc'></Option>
                    <Option value='videoAttr'></Option>

                </Select>
                <Input
                    placeholder=''
                    style={{ width: 150, margin: '0 15px' }}
                    value={searchName}
                    onChange={event => this.setState({ searchName: event.target.value })}
                />
                <Button type='primary' onClick={() => this.getVideos(1)}></Button>
            </span>
        )
        return (
            <Card title={head}>
                <Table rowKey='_id' loading={loading}
                    //
                    pagination={{ total, defaultPageSize: PAGE_SIZE, showQuickJumper: true, onChange: this.getVideos, }}
                    dataSource={videos}>
                    <Column title="" dataIndex="title" />
                    <Column title="" dataIndex="attr" />
                    <Column title="" dataIndex='time' />
                    <Column title="" dataIndex='director' />
                    <Column title="" dataIndex='main_actor' />
                    <Column
                        title=""
                        key="action"
                        render={(video) => (
                            <span>
                                <LinkButton onClick={() => this.props.history.push('/media/videodetail', { video })}></LinkButton>
                                <Divider type="vertical" />
                                <LinkButton onClick={() => this.deleteVideo(video)}></LinkButton>
                            </span>
                        )}
                    />
                </Table>
            </Card>
        )

    }
}
export default VideoManage;