import React, { Component } from 'react';
import { Card, Form, Input, Button, message, Select } from 'antd';
import VideoWall from './VideoWall'
import { reqAddVideos } from '../../api'
import './index.css'
const { TextArea } = Input
const { Item } = Form
const { Option } = Select;

class VideoUpload extends Component {
    constructor(props) {
        super(props)
        // ref
        this.pw = React.createRef()
    }
    submit = () => {
        // , , 
        this.props.form.validateFields(async (error, values) => {
            if (!error) {
                //1.23.
                const { title, desc, attr, main_actor, director } = values
                const url = this.pw.current.getUrls()
                const video = { title, desc, attr, url, main_actor, director }
                const result = await reqAddVideos(video)
                if (result.err === 0) {
                    message.success('')
                    this.props.history.push('/success', { video })
                }
                else {
                    message.error('')
                }
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: {
                xs: { span: 2 },//
                sm: { span: 2 },
            },
            wrapperCol: {
                xs: { span: 14 },//
                sm: { span: 14 },
            },
        };

        const { getFieldDecorator } = this.props.form;
        return (
            <Card>
                <Form {...formItemLayout}>
                    <Item label="">
                        {getFieldDecorator('attr', {
                            rules: [{ required: true, message: '!' }],
                        })(
                            <Select
                                placeholder=""
                                onChange={this.handleSelectChange}
                            >
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>,
                        )}
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('title', {
                                initialValue: '',
                                rules: [
                                    { required: true, message: '' },
                                    { max: 50, message: '' }//
                                ]
                            })(<Input placeholder='' />)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('director', {
                                initialValue: '',
                                rules: [
                                    { required: true, message: '' },
                                    { max: 30, message: '' }//
                                ]
                            })(<Input placeholder='' />)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('main_actor', {
                                initialValue: '',
                                rules: [
                                    { required: true, message: '' },
                                    { max: 100, message: '' }//
                                ]
                            })(<Input placeholder='' />)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('desc', {
                                initialValue: '',
                                rules: [
                                    { required: true, message: '' },
                                ]
                            })(<TextArea autosize={{ minRows: 2, maxRows: 6 }} placeholder=''></TextArea>)
                        }

                    </Item>
                    <Item className='soft'>
                        <VideoWall ref={this.pw} />
                    </Item>
                    <Item>
                        <Button style={{ marginLeft: '14%' }}
                            type='primary' block
                            onClick={this.submit}></Button>
                    </Item>
                </Form>
            </Card>
        )
    }
}

export default Form.create()(VideoUpload);