import React from 'react'
import { Upload, Icon, Modal, message } from 'antd'
import { reqDeleteVideo } from '../../api'
/*

 */
export default class VideoWall extends React.Component {


    state = {
        previewVisible: false, // Modal
        previewImage: '', // url
        fileList: [],
    }
    /*
      
       */
    getUrls = () => {
        return this.state.fileList.map(file => file.name)
    };

    /*
    Modal
     */
    handleCancel = () => this.setState({ previewVisible: false });

    handlePreview = file => {
        console.log('handlePreview()', file)
        // file
        this.setState({
            previewImage: file.url || file.thumbUrl,
            previewVisible: true,
        });
    };

    /*
    file: (/)
    fileList: 
     */
    handleChange = async ({ file, fileList }) => {
        console.log('handleChange()', file.status, fileList.length, file === fileList[fileList.length - 1])

        // , file(name, url)
        if (file.status === 'done') {
            const result = file.response  // {status: 0, data: {name: 'xxx.jpg', url: ''}}
            console.log(result)
            if (result.status === 0) {
                message.success('!')
                const { name, url } = result.data
                file = fileList[fileList.length - 1]
                file.name = name
                file.url = url
            } else {
                message.error('')
            }
        } else if (file.status === 'removed') { // 
            const result = await reqDeleteVideo(file.name)
            if (result.status === 0) {
                message.success('!')
            } else {
                message.error('!')
            }
        }

        // (/)fileList
        this.setState({ fileList })
    };

    render() {
        const { previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
            <div>
                <Icon type="plus" />
                <div>Upload</div>
            </div>
        );
        return (
            <div>
                <Upload
                    action="/videoFile/uploads" /**/
                    accept=".mp4, .rmvb, .avi"  /**/
                    name='video' /**/
                    listType="picture-card"  /**/
                    fileList={fileList}  /**/
                    onPreview={this.handlePreview}
                    onChange={this.handleChange}
                >
                    {fileList.length >= 100 ? null : uploadButton}
                </Upload>

                <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                    <video alt="video" style={{ width: '100%' }} src={previewImage} />
                </Modal>
            </div>
        );
    }
}