import React, { Component } from 'react';
import { Table, Divider, message, Modal, Select, Button, Input, Card } from 'antd';
import { PAGE_SIZE } from '../../utils/constants';
import LinkButton from '../../components/link-button';
import { reqDepartmentMessages, reqDeleteMessages, reqSearchMessages } from '../../api'
const { Column } = Table;
const Option = Select.Option

class MessageManage extends Component {
    state = {
        total: 0, // 
        Dmessages: [], // 
        loading: false,//
        searchName: '', // 
        searchType: 'noticeTitle', // 
    }
    //
    getBookFiles = async (pageNum) => {
        //loadingtrue
        this.setState({ loading: true })
        const { searchName, searchType } = this.state
        let result
        if (searchName) {
            result = await reqSearchMessages({ pageNum, pageSize: PAGE_SIZE, searchName, searchType })
        } else { // 
            result = await reqDepartmentMessages(pageNum, PAGE_SIZE)
        }
        this.setState({ loading: false })
        if (result.status === 0) {
            const { total, list } = result.data
            console.log(list)
            this.setState({
                total,
                Dmessages: list
            })
        }
    }
    /*

*/
    deleteBookFile = (Dmessage) => {
        Modal.confirm({
            title: `****[${Dmessage.title}]****?`,
            onOk: async () => {
                const result = await reqDeleteMessages(Dmessage.title)
                if (result.err === 0) {
                    message.success('!')
                    this.getBookFiles(1)
                } else if (result.err === -888) {
                    message.error(',')
                }else{
                    message.error('other error')
                }
            }
        })
    }
    componentDidMount() {
        this.getBookFiles(1)
    }
    render() {
        const { Dmessages, total, loading, searchName, searchType } = this.state

        const head = (
            <span>
                <Select
                    value={searchType}
                    style={{ width: 150 }}
                    onChange={value => this.setState({ searchType: value })}
                >
                    <Option value='noticeTitle'></Option>
                    <Option value='noticeAuthor'></Option>
                    <Option value='noticeContent'></Option>
                    <Option value='noticeDepartment'></Option>

                </Select>
                <Input
                    placeholder=''
                    style={{ width: 150, margin: '0 15px' }}
                    value={searchName}
                    onChange={event => this.setState({ searchName: event.target.value })}
                />
                <Button type='primary' onClick={() => this.getBookFiles(1)}></Button>
            </span>
        )
        return (
            <Card title={head}>
                <Table

                    rowKey='_id'
                    loading={loading}
                    //
                    pagination={{
                        total,
                        defaultPageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        onChange: this.getBookFiles,

                    }}
                    dataSource={Dmessages}>
                    <Column title="title" dataIndex="title" />
                    <Column title="" dataIndex="author" />
                    <Column title="" dataIndex="department" />
                    <Column title="" dataIndex='time' />
                    <Column
                        title=""
                        key="action"
                        render={(Dmessage) => (
                            <span>
                                <LinkButton onClick={() => this.props.history.push('/departmentMessage/detail', {Dmessage })}></LinkButton>
                                <Divider type="vertical" />
                                <LinkButton onClick={() => this.props.history.push('/departmentMessage/write', Dmessage)}></LinkButton>
                                <Divider type='vertical' />
                                <LinkButton onClick={() => this.deleteBookFile(Dmessage)}></LinkButton>
                            </span>
                        )}
                    />
                </Table>
            </Card>
        )

    }
}
export default MessageManage;